import { Component } from "react";

class Pio extends Component {
  render() {
    return (
      <div className="pio_sec">
        <div className="row">
          <div className="col-5 col-lg-5 col-md-5 ">
            <div className="name">
              <h1>Abd-Elrahman Sherwida</h1>
            </div>
            <div className="job">
              <h3>Front-End Player</h3>
            </div>
            <div className="qout">
              <p className="typing-erase">
                it's not just about what we can do, it also about what we love
                to do and I really love coding
              </p>
            </div>
          </div>
          <div className="col-7 col-lg-7 col-md-7 ">
            <div className="pio_sec-bg">
              <img
                src={`${process.env.PUBLIC_URL}/imgs/background.jpg`}
                alt="contact"
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default Pio;
